import React from 'react';
import Image, { StaticImageData } from 'next/image';
import { formattedPrice } from 'helpers/utils';

import styles from './ProductItem.module.scss';
import checkout from '/public/static/img/checkout.svg';
import checkoutActive from '/public/static/img/checkoutActive.svg';
import closeDark from 'public/static/img/closeDark.svg';
import checkbox from '/public/static/img/checkbox.svg';

import Button from 'components/UI/Button';
import { useSelector } from 'react-redux';
import { SuisseIntl } from 'helpers/utils/fonts';
import { ApiResp } from 'api/checkout/AddProductApi/types';
import { AddProductModel } from 'api/checkout/Models/AddProductModel';
import { getCheckoutProduct } from 'store/product/CheckoutProductStore';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import ym from 'react-yandex-metrika';

type ProductItemTypes = {
	data: {
		id?: number;
		img: StaticImageData | string;
		name: string;
		price: number;
		color: string;
		sizeList?: {
			id: number;
			name: string;
		}[];
		size?: string;
		isSelect?: boolean;
	};
	isFullInfo?: boolean;
	isReturned?: boolean;
	statusText?: string;
	isOrder?: boolean;
	orderSuccess?: boolean;
	isReviwe?: boolean;
	isBack?: boolean;
	className?: string;
	addReview?: () => void;
	selectProductBack?: () => void;
	closeClickHandler?: () => void;
	addInCheckoutHandler?: () => Promise<ApiResp<AddProductModel>>;
};

const ProductItem = ({
	data,
	isFullInfo,
	isReturned,
	statusText,
	isOrder,
	orderSuccess,
	isReviwe,
	isBack,
	className,
	addReview,
	selectProductBack,
	closeClickHandler,
	addInCheckoutHandler,
}: ProductItemTypes) => {
	const [isHover, setIsHover] = React.useState<boolean>(false);
	const [isOpenDitails, setIsOpenDitails] = React.useState<boolean>(false);
	const [isAddedInCheckout, setIsAddedInCheckout] = React.useState<boolean | 'error'>(false);
	const [addedInCheckoutSize, setAddedInCheckoutSize] = React.useState<string>(
		data.sizeList ? (data.sizeList[0] ? data.sizeList[0].name : '') : ''
	);
	const [checkoutIsHover, setCheckoutIsHover] = React.useState<boolean>(false);
	const [isCheckoutClicked, setIsCheckoutClicked] = React.useState<boolean>(false);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const links = useSelector((state: any) => state.breadcrumbsProduct.links);
	const checkoutProduct = useSelector((state: any) => state.checkoutProduct.checkoutProduct);

	const dispatch = useAppDispatch();

	const onMouseHandler = (isHoverVal: boolean) => {
		if (isHoverVal) {
			setIsHover(true);
		} else {
			setIsHover(false);
		}
	};

	const onClickCheckoutHandler = () => {
		if (isCheckoutClicked) return;
		if (addedInCheckoutSize === '') {
			addInCheckoutHandler &&
				addInCheckoutHandler().then(res => {
					if (res.data.errors && res.data.errors.id) {
						setIsAddedInCheckout('error');
					} else if (res.data.message === 'success') {
						setIsAddedInCheckout(true);
						setIsOpenDitails(!isOpenDitails);
						process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID && ym('reachGoal', 'to-cart');
						process.env.NEXT_PUBLIC_IS_PRODUCTION === 'true' &&
							(window as any).dataLayer.push({
								ecommerce: {
									currencyCode: 'RUB',
									add: {
										products: [
											{
												id: data.id,
												name: data.name,
												category: links.map(item => item.name).join('/'),
												price: data.price,
												variant: data.color,
											},
										],
									},
								},
							});
					}
				});
		} else {
			setIsOpenDitails(!isOpenDitails);
		}
	};

	const addInCheckout = (event: React.MouseEvent<HTMLButtonElement>) => {
		if (isCheckoutClicked) return;
		addInCheckoutHandler &&
			addInCheckoutHandler().then(res => {
				if (res.data.errors && res.data.errors.id) {
					setIsAddedInCheckout('error');
				} else if (res.data.message === 'success') {
					setIsAddedInCheckout(true);
				}
			});
		setAddedInCheckoutSize(isMobile ? addedInCheckoutSize : event.currentTarget.textContent);
	};

	const returnInFavorites = () => {
		setIsAddedInCheckout(false);
		setIsOpenDitails(!isOpenDitails);
		setAddedInCheckoutSize(data.sizeList ? (data.sizeList[0] ? data.sizeList[0].name : '') : '');
		dispatch(getCheckoutProduct({ token: localStorage.getItem('token') || null }));
	};

	const editProductSize = (event: React.MouseEvent<HTMLButtonElement>) => {
		setAddedInCheckoutSize(event.currentTarget.textContent);
	};

	const onCheckoutHandler = (isHoverVal: boolean) => {
		setCheckoutIsHover(isHoverVal);
	};

	React.useEffect(() => {
		setIsCheckoutClicked(checkoutProduct.some(item => item === data.id));
	}, [checkoutProduct]);

	return (
		<div
			onMouseEnter={() => onMouseHandler(true)}
			onMouseLeave={() => onMouseHandler(false)}
			className={`${styles.favoritesItem} ${isFullInfo ? styles.isFullInfo : ''} ${className ? className : ''} ${
				isBack ? styles.isBack : ''
			}`}
		>
			{isHover && !isMobile && !isFullInfo && (
				<div className={styles.close}>
					<Image
						alt="X"
						src={closeDark}
						width={24}
						height={24}
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
						onClick={closeClickHandler}
					/>
				</div>
			)}
			{!isMobile && !isFullInfo ? (
				<>
					<Image
						alt={data.name || 'product'}
						src={data.img}
						onLoadingComplete={img => (img.style.backgroundImage = 'none')}
						width={80}
						height={100}
						style={{
							maxWidth: '100%',
						}}
					/>
					<div className={styles.infoBlock}>
						{!isOpenDitails ? (
							<>
								<p>{data.name}</p>
								<p>{formattedPrice(data.price)}</p>
							</>
						) : (
							<>
								{!isAddedInCheckout ? (
									<>
										<p>Цвет: {data.color}</p>
										<div className={styles.sizes}>
											<p>Выберите размер</p>
											{data.sizeList.length !== 0 &&
												data.sizeList.map(item => {
													return (
														<button
															className={SuisseIntl.className}
															key={item.id}
															onClick={addInCheckout}
														>
															{item.name}
														</button>
													);
												})}
										</div>
									</>
								) : isAddedInCheckout === 'error' ? (
									<>
										<p>Размер {addedInCheckoutSize} уже добавлен в корзину!</p>
										<p onClick={returnInFavorites} className={`${styles.ok} ${SuisseIntl.className}`}>
											ОК
										</p>
									</>
								) : (
									<>
										<p>
											{addedInCheckoutSize === '' ? 'Товар' : 'Размер'} {addedInCheckoutSize} успешно
											добавлен в корзину!
										</p>
										<p onClick={returnInFavorites} className={`${styles.ok} ${SuisseIntl.className}`}>
											ОК
										</p>
									</>
								)}
							</>
						)}
						{!isAddedInCheckout && !isOpenDitails && (
							<Image
								alt="checkout"
								onClick={onClickCheckoutHandler}
								onMouseEnter={() => onCheckoutHandler(true)}
								onMouseLeave={() => onCheckoutHandler(false)}
								src={isCheckoutClicked || checkoutIsHover ? checkoutActive : checkout}
								width={isMobile ? 20 : 24}
								height={isMobile ? 20 : 24}
								style={{
									maxWidth: '100%',
									height: 'auto',
								}}
							/>
						)}
					</div>
				</>
			) : (
				<>
					{!isFullInfo && (
						<div className={styles.close}>
							<Image
								alt="X"
								src={closeDark}
								width={16}
								height={16}
								style={{
									maxWidth: '100%',
									height: 'auto',
								}}
								onClick={closeClickHandler}
							/>
						</div>
					)}
					<div
						style={isBack ? { cursor: 'pointer' } : {}}
						className={styles.card}
						onClick={isBack ? selectProductBack : null}
					>
						<Image
							alt={data.name || 'product'}
							src={data.img}
							onLoadingComplete={img => (img.style.backgroundImage = 'none')}
							width={134}
							height={167}
							style={{
								maxWidth: '100%',
								height: 'auto',
							}}
						/>
						{isBack && (
							<div className={styles.checkbox}>
								{data.isSelect && <Image src={checkbox} width={12} height={12} alt="V" />}
							</div>
						)}
						{isReturned && statusText && <p className={styles.isReturned}>{statusText}</p>}
						<div className={styles.infoBlock}>
							{!isAddedInCheckout ? (
								<>
									<div>
										<p>{data.name}</p>
										<p>{formattedPrice(data.price)}</p>
									</div>
									<div>
										<p>Цвет: {data.color}</p>
										<div className={styles.sizes}>
											{!isFullInfo && (
												<>
													<p>Выберите размер</p>
													<>
														{data.sizeList.length !== 0 &&
															data.sizeList.map(item => {
																return (
																	<button
																		key={item.id}
																		className={
																			addedInCheckoutSize === item.name
																				? styles.activeSize
																				: ''
																		}
																		onClick={editProductSize}
																	>
																		{item.name}
																	</button>
																);
															})}
													</>
												</>
											)}
											{isFullInfo && <p>Размер: {data.size}</p>}
										</div>
									</div>
								</>
							) : (
								<>
									<div>
										<p>Размер {addedInCheckoutSize} успешно добавлен в корзину!</p>
										<p onClick={returnInFavorites} className={`${styles.ok} ${SuisseIntl.className}`}>
											ОК
										</p>
									</div>
								</>
							)}
						</div>
					</div>
					{!isOrder && !isReviwe && !isBack && !isAddedInCheckout && (
						<Button
							onClick={addInCheckout}
							className={styles.button}
							text={isCheckoutClicked ? 'В корзине' : 'Добавить в корзину'}
							isInvisible
							disabled={isCheckoutClicked}
						/>
					)}
					{isOrder && !isReturned && orderSuccess && (
						<Button onClick={addReview} className={styles.button} text="Оставить отзыв" isInvisible />
					)}
				</>
			)}
		</div>
	);
};

export default React.memo(ProductItem);
