import { SuisseIntl } from 'helpers/utils/fonts';
import React from 'react';

import styles from './SubTitle.module.scss';

type SubTitleProps = {
	id?: string;
	size?: number;
	text?: any;
	withDot?: boolean;
	className?: string;
	withSuisse?: boolean;
	withUpperCase?: boolean;
	marginTop?: number;
	marginBottom?: number;
	children?: React.ReactChildren | string;
};

const SubTitle = ({
	id,
	size,
	text,
	withDot,
	className,
	withSuisse,
	withUpperCase,
	marginTop,
	marginBottom,
	children,
}: SubTitleProps) => {
	return (
		<h2
			id={id}
			style={{
				marginTop: Number.isInteger(marginTop) ? marginTop : null,
				marginBottom: Number.isInteger(marginBottom) ? marginBottom : null,
				fontSize: size ? size : null,
				textTransform: withUpperCase ? 'uppercase' : null,
			}}
			className={`${styles.subTitle} ${className ? className : ''} ${withSuisse ? SuisseIntl.className : ''}`}
		>
			{withDot ? '· ' : ''}
			{children ? children : text}
		</h2>
	);
};

export default React.memo(SubTitle);
